"use client";

import { useState } from "react";
import {
  Edit3,
  Trash2,
  Check,
  X,
  Plus,
} from "lucide-react";

// Peso formatter
const currency = (val = 0) =>
  new Intl.NumberFormat("en-PH", {
    style: "currency",
    currency: "PHP",
    minimumFractionDigits: 2,
  }).format(val);

export default function TransactionTable({
  transactions = [],
  loading,
  onAddTransaction,
  onEditTransaction,
  onDeleteTransaction,
}) {
  const [showForm, setShowForm] = useState(false);
  const [editingId, setEditingId] = useState(null);

  const [form, setForm] = useState({
    date: "",
    description: "",
    deposit: "",
    withdrawal: "",
  });

  const [rowForm, setRowForm] = useState({
    date: "",
    description: "",
    deposit: "",
    withdrawal: "",
  });

  const handleAddSubmit = (e) => {
    e.preventDefault();
    if (!form.date || (!form.deposit && !form.withdrawal)) return;

    onAddTransaction({
      date: form.date,
      description: form.description,
      deposit: Number(form.deposit || 0),
      withdrawal: Number(form.withdrawal || 0),
    });

    setForm({ date: "", description: "", deposit: "", withdrawal: "" });
    setShowForm(false);
  };

  const startEdit = (t) => {
    setEditingId(t.id);
    setRowForm({
      date: t.date,
      description: t.description,
      deposit: t.deposit || "",
      withdrawal: t.withdrawal || "",
    });
  };

  const saveEdit = (id) => {
    onEditTransaction(
      {
        date: rowForm.date,
        description: rowForm.description,
        deposit: Number(rowForm.deposit || 0),
        withdrawal: Number(rowForm.withdrawal || 0),
      },
      id
    );
    setEditingId(null);
  };

  return (
    <div className="space-y-6">

  
      {/* ADD FORM */}
      {showForm && (
        <div className="border rounded-lg bg-slate-50 p-4">
          <form
            onSubmit={handleAddSubmit}
            className="grid grid-cols-1 md:grid-cols-5 gap-3"
          >
            <input
              type="date"
              className="border rounded px-3 py-2 text-sm"
              value={form.date}
              onChange={(e) => setForm({ ...form, date: e.target.value })}
              required
            />
            <input
              type="text"
              placeholder="Description"
              className="border rounded px-3 py-2 text-sm"
              value={form.description}
              onChange={(e) => setForm({ ...form, description: e.target.value })}
            />
            <input
              type="number"
              placeholder="Deposit"
              className="border rounded px-3 py-2 text-sm text-right"
              value={form.deposit}
              onChange={(e) => setForm({ ...form, deposit: e.target.value })}
            />
            <input
              type="number"
              placeholder="Withdrawal"
              className="border rounded px-3 py-2 text-sm text-right"
              value={form.withdrawal}
              onChange={(e) => setForm({ ...form, withdrawal: e.target.value })}
            />
            <div className="flex gap-2">
              <button
                type="submit"
                className="flex-1 bg-emerald-600 text-white rounded px-3 py-2 text-sm hover:bg-emerald-700"
              >
                Save
              </button>
              <button
                type="button"
                onClick={() => setShowForm(false)}
                className="flex-1 border rounded px-3 py-2 text-sm"
              >
                Cancel
              </button>
            </div>
          </form>
        </div>
      )}

      {/* TABLE */}
      <div className="border rounded-lg overflow-hidden bg-white">
        <table className="w-full text-sm">
          <thead className="bg-slate-100 text-slate-700">
            <tr>
              <th className="px-4 py-3 text-left font-medium">Date</th>
              <th className="px-4 py-3 text-left font-medium">Description</th>
              <th className="px-4 py-3 text-right font-medium">Deposit</th>
              <th className="px-4 py-3 text-right font-medium">Withdrawal</th>
              <th className="px-4 py-3 text-right font-medium">Balance</th>
              <th className="px-4 py-3 text-center font-medium">Actions</th>
            </tr>
          </thead>

          <tbody>
            {transactions.map((t) => (
              <tr
                key={t.id}
                className="border-t hover:bg-slate-50"
              >
                {editingId === t.id ? (
                  <>
                    <td className="px-4 py-2">
                      <input
                        type="date"
                        className="border rounded px-2 py-1 w-full text-sm"
                        value={rowForm.date}
                        onChange={(e) =>
                          setRowForm({ ...rowForm, date: e.target.value })
                        }
                      />
                    </td>
                    <td className="px-4 py-2">
                      <input
                        className="border rounded px-2 py-1 w-full text-sm"
                        value={rowForm.description}
                        onChange={(e) =>
                          setRowForm({ ...rowForm, description: e.target.value })
                        }
                      />
                    </td>
                    <td className="px-4 py-2">
                      <input
                        type="number"
                        className="border rounded px-2 py-1 w-full text-right text-sm"
                        value={rowForm.deposit}
                        onChange={(e) =>
                          setRowForm({ ...rowForm, deposit: e.target.value })
                        }
                      />
                    </td>
                    <td className="px-4 py-2">
                      <input
                        type="number"
                        className="border rounded px-2 py-1 w-full text-right text-sm"
                        value={rowForm.withdrawal}
                        onChange={(e) =>
                          setRowForm({ ...rowForm, withdrawal: e.target.value })
                        }
                      />
                    </td>
                    <td className="px-4 py-2 text-right font-semibold">
                      {currency(t.balance)}
                    </td>
                    <td className="px-4 py-2 flex justify-center gap-2">
                      <button
                        onClick={() => saveEdit(t.id)}
                        className="text-emerald-600 hover:text-emerald-700"
                      >
                        <Check size={16} />
                      </button>
                      <button
                        onClick={() => setEditingId(null)}
                        className="text-slate-500 hover:text-slate-700"
                      >
                        <X size={16} />
                      </button>
                    </td>
                  </>
                ) : (
                  <>
                    <td className="px-4 py-2">{t.date}</td>
                    <td className="px-4 py-2">{t.description || "-"}</td>
                    <td className="px-4 py-2 text-right text-emerald-700">
                      {t.deposit ? currency(t.deposit) : "‚Äî"}
                    </td>
                    <td className="px-4 py-2 text-right text-rose-700">
                      {t.withdrawal ? currency(t.withdrawal) : "‚Äî"}
                    </td>
                    <td className="px-4 py-2 text-right font-semibold">
                      {currency(t.balance)}
                    </td>
                    <td className="px-4 py-2 flex justify-center gap-2">
                      <button
                        onClick={() => startEdit(t)}
                        className="text-slate-600 hover:text-slate-800"
                      >
                        <Edit3 size={16} />
                      </button>
                      <button
                        onClick={() => onDeleteTransaction(t.id)}
                        className="text-rose-600 hover:text-rose-700"
                      >
                        <Trash2 size={16} />
                      </button>
                    </td>
                  </>
                )}
              </tr>
            ))}

            {!transactions.length && (
              <tr>
                <td colSpan="6" className="px-6 py-8 text-center text-slate-500">
                  No transactions recorded
                </td>
              </tr>
            )}
          </tbody>
        </table>

        {loading && (
          <div className="p-4 text-center text-slate-500">
            Loading transactions‚Ä¶
          </div>
        )}
      </div>
    </div>
  );
}
